import { useEffect, useState } from 'react';
import * as React from 'react';

import { useDispatch, useSelector } from 'react-redux';

import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import IconButton from '@mui/material/IconButton';
import AutorenewIcon from '@mui/icons-material/Autorenew';

import { getMonoToday } from 'store/thunks';

import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import { currencyMonoTodayStatus } from 'store/actions';

export const CurrencyMono = () => {
  var moment = require('moment');
  //     
  const dataMono = useSelector(state => state.storeCurrencyMonoToday.data);
  const TIME = useSelector(state => state.storeCurrencyMonoToday.time);
  const STATUS = useSelector(state => state.storeCurrencyMonoToday.status);
  // const TIME = '2024-09-23';
  // const STATUS = true;

  //     
  const [USD, setUSD] = useState([]);
  const [PLN, setPLN] = useState([]);
  const [EUR, setEUR] = useState([]);
  const [RUB, setRUB] = useState([]);
  const [BYN, setBYN] = useState([]);
  const [eurTOusaSell, setEurTOusaSell] = useState('-,----');
  const [eurTOusaBuy, setEurTOusaBuy] = useState('-,----');
  const [usdTOeurSell, setUsdTOeurSell] = useState('-,----');
  const [usdTOeurBuy, setUsdTOeurBuy] = useState('-,----');

  useEffect(() => {
    if (dataMono.length !== 0) {
      setUSD(dataMono.find(el => el.currencyCodeA === 840));
      setPLN(dataMono.find(el => el.currencyCodeA === 985));
      setEUR(dataMono.find(el => el.currencyCodeA === 978));
      setRUB(dataMono.find(el => el.currencyCodeA === 943));
      setBYN(dataMono.find(el => el.currencyCodeA === 933));
      setEurTOusaSell((EUR.rateSell / USD.rateSell).toFixed(4));
      setEurTOusaBuy((EUR.rateBuy / USD.rateBuy).toFixed(4));
      setUsdTOeurSell((USD.rateSell / EUR.rateSell).toFixed(4));
      setUsdTOeurBuy((USD.rateBuy / EUR.rateBuy).toFixed(4));
    }
  }, [
    EUR.rateBuy,
    EUR.rateSell,
    USD.rateBuy,
    USD.rateSell,
    dataMono,
    eurTOusaBuy,
    eurTOusaSell,
    usdTOeurBuy,
    usdTOeurSell,
  ]);

  // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  // !!!        !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!

  //     
  const [valueSelect1, setValueSelect1] = useState('UAH');
  const [valueSelect2, setValueSelect2] = useState('EUR');
  //    
  const [valueText1, setValueText1] = useState('1');
  const [valueText2, setValueText2] = useState('0');
  //   ,   
  const [textField_On_1, setTextField_On_1] = useState(true);
  const [textField_On_2, setTextField_On_2] = useState(false);
  //     
  const [d1, setD1] = useState(['UAH', 'USD', 'PLN', 'RUB', 'BYN']);
  const [d2, setD2] = useState(['EUR', 'USD', 'PLN', 'RUB', 'BYN']);

  //      1
  const handleChange1 = event => {
    setValueSelect1(event.target.value);
  };

  //      2
  const handleChange2 = event => {
    setValueSelect2(event.target.value);
  };

  //     d1, d2
  useEffect(() => {
    const arr1 = ['UAH', 'EUR', 'USD', 'PLN', 'RUB', 'BYN'];
    let a1 = [...arr1];
    let d1 = [...a1.splice(a1.indexOf(valueSelect1), 1), ...a1];
    const e1 = d1.indexOf(valueSelect2);
    d1.splice(e1, 1);
    setD1(d1);
    let a2 = [...arr1];
    let d2 = [...a2.splice(a2.indexOf(valueSelect2), 1), ...a2];
    const e2 = d2.indexOf(valueSelect1);
    d2.splice(e2, 1);
    setD2(d2);
  }, [valueSelect1, valueSelect2]);

  //    
  const handleExpanr = () => {
    setD1(d2);
    setD2(d1);
    const temp1 = valueSelect1;
    const temp2 = valueSelect2;
    setValueSelect1(temp2);
    setValueSelect2(temp1);
  };

  //          1
  const handleConvert1 = e => {
    setValueText1(e.target.value);
    setTextField_On_1(true);
    setTextField_On_2(false);
  };

  //          2
  const handleConvert2 = e => {
    setValueText2(e.target.value);
    setTextField_On_1(false);
    setTextField_On_2(true);
  };

  //            .       
  useEffect(() => {
    //       switch
    let a = 1;
    let b = 1;

    //      "a"
    switch (valueSelect1) {
      case 'USD':
        a = USD.rateBuy;
        break;
      case 'EUR':
        a = EUR.rateBuy;
        break;
      case 'UAH':
        a = 1;
        break;
      case 'PLN':
        a = PLN.rateCross;
        break;
      case 'RUB':
        a = RUB.rateCross;
        break;
      case 'BYN':
        a = BYN.rateCross;
        break;
      default:
        break;
    }
    //      "b"
    switch (valueSelect2) {
      case 'USD':
        b = USD.rateBuy;
        break;
      case 'EUR':
        b = EUR.rateBuy;
        break;
      case 'UAH':
        b = 1;
        break;
      case 'PLN':
        b = PLN.rateCross;
        break;
      case 'RUB':
        b = RUB.rateCross;
        break;
      case 'BYN':
        b = BYN.rateCross;
        break;
      default:
        break;
    }

    //      
    let c = (Number(valueText1) * (a / b)).toFixed(2);
    let d = (Number(valueText2) * (b / a)).toFixed(2);

    //       
    if (textField_On_1) {
      setValueText2(c);
    } else if (textField_On_2) {
      setValueText1(d);
    }
  }, [
    BYN.rateCross,
    EUR.rateBuy,
    PLN.rateCross,
    RUB.rateCross,
    USD.rateBuy,
    textField_On_1,
    textField_On_2,
    valueSelect1,
    valueSelect2,
    valueText1,
    valueText2,
  ]);

  // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
  // !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!

  let dateToday = moment().format('DD MMMM YYYY');

  const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    [`&.${tableCellClasses.body}`]: {
      fontSize: 14,
    },
  }));

  const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
    '&:last-child td, &:last-child th': {
      border: 0,
    },
  }));

  function createData(name, rateBuy, rateSell) {
    return { name, rateBuy, rateSell };
  }

  const rows = [
    createData(' ', USD.rateBuy, USD.rateSell),
    createData('', EUR.rateBuy, EUR.rateSell),
    createData(' ', RUB.rateCross, RUB.rateCross),
    createData(' ', BYN.rateCross, BYN.rateCross),
    createData('', PLN.rateCross, PLN.rateCross),
  ];

  //   
  const dispatch = useDispatch();
  const handleUpdateCurrency = () => {
    dispatch(getMonoToday());
    dispatch(currencyMonoTodayStatus(false));
  };

  return (
    <div>
      <div className="nameSection">
        <IconButton
          color="primary"
          aria-label="add to shopping cart"
          onClick={handleUpdateCurrency}
        >
          <AutorenewIcon />
        </IconButton>
        <h2> .   {TIME}</h2>
        {STATUS ? (
          <CheckCircleOutlineIcon color="success" />
        ) : (
          <WarningAmberIcon color="warning" />
        )}
      </div>

      <div className="converter-block">
        <Box
          className="textFieldBlock"
          component="form"
          // sx={{'& > :not(style)': { m: 1, width: '25ch' },}}
          noValidate
          autoComplete="off"
        >
          <TextField
            className="textField"
            id="cur1"
            // label=""
            type="number"
            min="0"
            value={valueText1}
            onChange={handleConvert1}
            variant="standard"
            step="1.00"
            color="warning"
          />
          <TextField
            className="textField"
            id="cor2"
            // label=""
            type="number"
            min="0"
            value={valueText2}
            onChange={handleConvert2}
            variant="standard"
            step="1.00"
            // color="warning"
            // style={{ color: '#fff' }}
          />
        </Box>

        <div className="currencyBTN">
          <FormControl
            className="currencyBtnSelect"
            variant="filled"
            // sx={{ m: 1, minWidth: 200 }}
          >
            <InputLabel id="demo-simple-select-standard-label">
              
            </InputLabel>
            <Select
              labelId="demo-simple-select-standard-label"
              id="demo-simple-select-standard"
              value={valueSelect1}
              onChange={handleChange1}
              label="Converter"
              // color="warning"
            >
              {d1.map(i => (
                <MenuItem key={i} value={i}>
                  {i}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <IconButton
            className="currencyBtnRevert"
            color="primary"
            aria-label="add to shopping cart"
            onClick={handleExpanr}
          >
            <AutorenewIcon />
          </IconButton>

          <FormControl
            className="currencyBtnSelect"
            variant="filled"
            // sx={{ m: 1, minWidth: 200 }}
          >
            <InputLabel id="demo-simple-select-filled-label"></InputLabel>
            <Select
              labelId="demo-simple-select-filled-label"
              id="demo-simple-select-filled"
              value={valueSelect2}
              onChange={handleChange2}
              label="Converter"
              // color="warning"
            >
              {d2.map(i => (
                <MenuItem key={i} value={i}>
                  {i}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
      </div>

      <div className="nameSection">
        <IconButton
          color="primary"
          aria-label="add to shopping cart"
          onClick={handleUpdateCurrency}
        >
          <AutorenewIcon />
        </IconButton>
        <h2>
           MONObank  {dateToday}.   {TIME}
        </h2>
        {STATUS ? (
          <CheckCircleOutlineIcon color="success" />
        ) : (
          <WarningAmberIcon color="warning" />
        )}
      </div>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
              <StyledTableCell align="right"></StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => (
              <StyledTableRow key={row.name}>
                <TableCell component="th" scope="row">
                  {row.name}
                </TableCell>
                <StyledTableCell align="right">{row.rateBuy}</StyledTableCell>
                <StyledTableCell align="right">{row.rateSell}</StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};
